.ModalContainer {
  background: rgba($color: #000000, $alpha: 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 0.4s;
  height: 100vh;
  width: 100vw;
  margin: 0 auto;
}

.ComparisonResults {
  overflow: scroll;
  position: fixed;
  width: 95%;
  height: 90%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 3rem 2.5rem;
  background: white;
  border: 2px solid black;
  border-radius: 5px;
  text-align: center;

  &__heading {
    text-transform: uppercase;
    font-size: 2.4rem;
  }

  &__options {
    width: 100%;
    margin: 0 auto 5px auto;
    display: flex;
  }

  &__option {
    justify-content: space-between;
    flex: 1 1 33%;

    img {
      border: 2px solid black;
      border-radius: 5px;
      margin: 0 auto;
    }

    &--selected img {
      background: lighten($color-3, 5%);

      &:hover {
        background: $color-3;
      }
    }
  }

  &__details {
    flex: 1 1 33%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    p {
      display: inline-block;
      margin: 0 10px;
      border-bottom: 2px solid $color-2;

      &:not(:last-child) {
      margin-bottom: 1.6rem;
      }
    }
  }

  &__icon {
    max-width: 150px;
    padding: 2rem;
  }

  &__map {
    margin: 0 auto;
    width: 100%;
    max-width: 650px;
  }

  &__button--close {
    display: block;
    margin-left: auto;
    padding: 1rem 2rem;
    margin-bottom: 20px;
    border-radius: 5px;
    text-transform: uppercase;
    font-weight: 600;
    color: $color-1;
    background-color: $color-2;
    border: none;
    transition: 0.3s; 

    &:hover {
        background-color: $color-3;
    }
  }

  &__disclaimer {
    padding: 10px 0;
  }

  h2 {
    display: inline;
  }
}

.show-modal {
  &-enter {
    transform: translateY(100%);
  }

  &-enter-done {
    transform: translateY(0);
  }

  &-exit {
    transform: translateY(0);
  }

  &-exit-active {
    transform: translateY(100%);
  }
}
