// MEDIUM SCREENS =====================
@media(max-width: 1100px){
    //Change title styles
    h2{
        font-size: 2.5rem;
        margin-top: 0;
    }
}

@media(max-width: 1000px){
    .PodcastCard__button--showMore{
        display: block;
    }

    //Change max height of podcast description to match smaller screen
    .PodcastCard__description--snippet{
        max-height: 14rem;
    }
}

@media (max-width: 975px) { 
    .Header {
        &__displayArea {
            width: 100%;
            height: 100%;
            border-radius: 0;
        }
    }
}

@media (max-width: 800px) { 
    .MapQuest {
        &__form {
            flex-direction: column;
            align-items: center;

              span {
                  padding: 0;
                  margin: 0;
                  width: 80%;
              }
        }
    }

    .PodcastSearch {
        &__form {
            flex-direction: column;
            align-items: center;
                input {
                    width: 80%;
                    margin-bottom: 0;
            }
        }
    }

    .PodcastResults__item {
        flex-direction: column;
        
        &>div {
            display: inline-block;
            margin: 0 auto;
        }
    }

    .PodcastCard__submitButton {
        display: inline-block;
        margin: 0 auto;

    }

    .Episode__options--details {
        align-items: center;
    }

    .EpisodeResults__item{
        flex-direction: column;

        h2 {
            margin-top: 1rem;
            font-size: 2.25rem;
        }

        img {
            display: none;
        }
    }

}
// TABLET SCREENS ======================
@media (max-width: 640px) { 
    .Header {
            &__mainHeader::before {
            top: 120px;
        }
    }
}

// MOBILE SCREENS ========================
@media (max-width: 550px) { 
    .ComparisonResults {
        &__options {
            flex-wrap: wrap;
        }

        &__option {
            margin-bottom: 25px;

            img {
                width: 85%;
            }
        }

        &__details {
            flex: 1 1 100%;
            // flex-direction: row;
            order: 1;

            p {
                flex: 1 1 100%;
                padding: 3px;
                margin: 0;
                display: block;
            };
        };
    };
};

@media (max-width: 375px) { 
    html {
        font-size: 55%;
    } 
    
    .Header {
            &__mainHeader::before {
            top: 105px;
        }
    }
}